.cardGroup {
  @apply
  flex 
  flex-wrap 
  max-w-7xl 
  mx-auto 
  my-4
}

.continueButton {
  @apply
  mx-auto 
  text-center
  mt-2
}

.button {
  @apply 
  inline-flex 
  justify-center 
  rounded-md 
  border 
  border-neutral-300 
  shadow-sm
  shadow-zinc-900/10
  px-4
  py-2 
  bg-gradient-to-l from-blue-500 via-blue-600 to-blue-500
  text-sm 
  font-medium 
  text-white
  hover:bg-gradient-to-r 
  hover:from-blue-500
  hover:via-blue-700
  hover:to-blue-500
  hover:shadow-blue-700
  hover:shadow-inner
  mt-4
}

.buttonText {
  @apply 
  font-light
}

